import React from "react";
import SignUpReferenceLink from "components/reusable/atoms/links/SignUpReferenceLink";

const formatCompanyWording = (partnerName) =>
  partnerName.toLowerCase().includes("startup") ? "companies" : "startups";

const titleClass = (title) =>
  title.length > 85 ? "long-title" : "short-title";

const SignUp = ({
  partnerFullName,
  title,
  logoImage,
  partnerBenefits,
  alternativeJoinLine,
  alternativeCallToAction,
  alternativeContent,
  signupButtonText = "Get Your Card",
  partnerExtra,
}) => (
  <section className="partner-landing-signup">
    <div className={`signup-title ${titleClass(title)}`}>{title}</div>
    {partnerFullName && !alternativeJoinLine && (
      <div className="signup-subtitle">
        Join the growing list of modern {formatCompanyWording(partnerFullName)}{" "}
        with this exclusive offer for {partnerFullName} customers.
      </div>
    )}
    {alternativeJoinLine && (
      <div className="signup-subtitle">{alternativeJoinLine}</div>
    )}
    {partnerBenefits && partnerBenefits}
    {alternativeContent && alternativeContent}
    {logoImage && (
      <img alt="Brex Logo" className="logo-image" src={logoImage} />
    )}
    {alternativeCallToAction && (
      <div className="signup-subtitle">{alternativeCallToAction}</div>
    )}
    <SignUpReferenceLink className="orange-button orange-button--getcard">
      {signupButtonText}
    </SignUpReferenceLink>
    {partnerExtra}
    <div className="filler" />
  </section>
);

export default SignUp;
